.boss{
    width:7.5rem;
    .my-drawer {
        position: relative;
        overflow: auto;
        -webkit-overflow-scrolling: touch;
      }
      .my-drawer .am-drawer-sidebar {
        background-color: #fff;
        overflow: auto;
        -webkit-overflow-scrolling: touch;
      }
      .my-drawer .am-drawer-sidebar .am-list {
        width: 300px;
        padding: 0;
      }
      .am-drawer-content{
          padding-top:0.2rem !important;
      }
      .am-navbar{
          background:#d3544d !important;
      }
    .banner{
        width:7.5rem;
        height:4rem;
        background:#d75a50;
        border-radius:0.3rem;
    }
    .order-box{
        width:7.5rem;
        height:2.96rem;
        background:#fff;
        border-top:0.01rem solid #e1e1e1;
        border-bottom:0.01rem solid #e1e1e1;
        border-radius:0.2rem;
        p{
            width:100%;
            height:0.9rem;
            line-height:0.9rem;
            text-align:center;
            font-size:0.3rem;
            color:#333333;
        }
        .left{
            width:50%;
            height:2.06rem;
            float:left;
            position:relative;
            .big{
                width:0.84rem;
                height:0.84rem;
                border-radius:50%;
                background:#ec6b60;
                position:absolute;
                left:1.6rem;
                top:0.14rem;
                display:flex;
                justify-content: center;
                align-items:center;
                span{
                    display:block;
                    position:absolute;
                    font-size:0.36rem;
                    color:#fff; 
                }
            }
            p{
                width:1rem;
                position:absolute;
                top:1rem;
                left:1.5rem;
                text-align:center;
                font-size:0.3rem;
                color:#333333;
            }
        }
        .right{
            width:50%;
            height:2.06rem;
            float:left;
            position:relative;
            .big{
                width:0.84rem;
                height:0.84rem;
                border-radius:50%;
                background:#70d3c2;
                position:absolute;
                left:1.6rem;
                top:0.14rem;
                display:flex;
                justify-content: center;
                align-items:center;
                span{
                    display:block;
                    position:absolute;
                    font-size:0.36rem;
                    color:#fff; 
                }
            }
            p{
                width:1rem;
                position:absolute;
                top:1rem;
                left:1.5rem;
                text-align:center;
                font-size:0.3rem;
                color:#333333
            }
        }    
    }
    .bottom-box{
        width:7.5rem;
        background:#fff;
        border-top:0.01rem solid #e1e1e1;
        border-bottom:0.01rem solid #e1e1e1;
        ul{
            width:100%;
            padding-left:0.3rem;
            box-sizing:border-box;
            li{
                width:7.2rem;
                height:0.86rem;
                line-height:0.86rem;
                display:flex;
                align-items:center;
                justify-content: space-between;
                border-bottom:0.01rem solid #e1e1e1;
                font-size:0.3rem;
                color:#333333;
                .iconfont{
                    color:#cccccc;
                    font-size:0.26rem;
                }
            }
            li:last-child{
                border-bottom:0;
            }
        }
    }
}